// src/pages/Home.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import './Home.css';
import Navbar from './Navbar';
import Features from './Features';


function Home() {
  const navigate = useNavigate();
useEffect(() => {
    const fetchUser = async () => {
      try {
        const res = await fetch("https://86fb-2601-646-8981-83c0-419-8e0c-34a4-4b09.ngrok-free.app/api/auth/user", {
          credentials: "include", //  cookies
        });
        const data = await res.json();
        console.log(" ", data);

        if (data?.id) {
          localStorage.setItem("userId", data.id); //   userId
        }
      } catch (err) {
        console.error(" :", err);
      }
    };

    fetchUser();
  }, []);
  return (
    <>
    <Navbar />
    <div className="home-container">
      <div className="main-content">
        <div className="left-panel">
        <div className="text-wrapper">
          <h1>Tired of chaotic travel planning?</h1>
          <h2><span className="highlight">Plan together.</span> Remember forever.</h2>
          <div className="button-row">
                <button className="primary-btn" onClick={() => navigate('/create')}>
                    Start a New Plan
                </button>
                <button className="secondary-btn" onClick={() => navigate('/plans')}>
                    View Existing Plans
                </button>
          </div>
        </div>
        </div>
        </div>
      </div>
    <Features />
    </>
  );
}

export default Home;
